.page-header {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 400px;
  position: relative;
  z-index: 0;
  &::after {
    content: "";
    width: 100%;
    height: 100%;
    background: rgba(#0e0013, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
  }

  &__bg {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__content {
    transform: translateY(40px);
    text-align: center;
  }

  &__title {
    @extend .hero__title;
    width: 100%;
    max-width: 800px;
    margin: 0 auto !important;
    margin-bottom: 0 !important;
    &-sm {
      font-size: clamp(2rem, 0.596rem + 7.02vw, 3.5rem) !important;
      margin-bottom: 0 !important;
    }
  }

  &--nobg {
    &::after {
      display: none;
    }
  }
}
