$burger-color: var(--main-gradient);
$burger-width: 40px;

.btn-burger {
  cursor: pointer;
  width: $burger-width;
  height: $burger-width;
  background: transparent;
  outline: none;
  border: none;
  position: relative;
  transition: all 0.4s ease-in-out;

  &__line {
    width: 100%;
    height: 4px;
    background: $burger-color;
    position: absolute;
    top: 50%;
    left: 0;
    transition: all 0.2s ease-in-out;

    &::before,
    &::after {
      content: "";
      width: 100%;
      height: 4px;
      background: $burger-color;
      position: absolute;
      right: 0;
      opacity: 1;
      transition: transform 0.2s ease-in-out;
    }

    &::before {
      width: $burger-width;
      top: -10px;
    }

    &::after {
      width: 24px;
      bottom: -10px;
    }
  }

  &.active {
    .btn-burger__line {
      background: transparent;

      &::before {
        top: 20%;
        transform: rotate(45deg);
      }

      &::after {
        width: $burger-width;
        top: 0;
        bottom: 50%;
        transform: rotate(-45deg);
      }
    }
  }
}
