.header {
  width: 100%;
  background: rgba(11, 11, 12, 0.5);
  backdrop-filter: blur(15px);
  padding: 15px 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;

  &.fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
  }

  &__container {
    max-width: 1320px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;

    @media (min-width: 481px) {
      padding: 0 30px;
    }
  }

  &__left,
  &__right {
    display: flex;
    align-items: center;
    gap: 30px;
  }

  &__logo {
    flex-shrink: 0;
  }

  // burger {
  &__burger {
    display: none;
  }

  // menu
  .menu {
    list-style: none;
    margin: 0;
    display: flex;
    align-items: center;

    &__item {
      &:not(:last-child) {
        margin-right: 30px;
      }
    }

    &__link {
      display: inline-block;
      color: #fff;
      font-size: 16px;
      font-weight: 500;
      line-height: 1.2;
      text-transform: uppercase;
      letter-spacing: 0.5px;
      transition: color 0.2s ease-in-out;

      &:hover {
        text-decoration: none;
        color: var(--violet-color);
      }

      &.active {
        cursor: default;
        border: 1px solid #fff;
        color: #fff !important;
        padding: 10px 15px;
      }
    }
  }

  &__desk-contacts {
    display: flex;
    gap: 32px;
    align-items: center;

    @media (max-width: 767px) {
      display: none;
    }
  }

  &__mob-contacts {
    display: flex;
    flex-direction: column;
    gap: 32px;
    margin-top: 48px;
    display: none;

    @media (max-width: 767px) {
      display: flex;
    }
  }
}

.logo {
  cursor: pointer;
  display: inline-flex;
  align-items: center;

  img {
    flex-shrink: 0;
  }

  &__img-mob {
    display: none;
  }
}

.btn-submit-property {
  color: var(--main-color);
  background-color: transparent;
  border: 1px solid var(--main-color);
  font-family: var(--font-default);
  font-size: 20px;
  font-weight: 500;
  text-transform: uppercase;
  display: inline-block;
  padding: 15px 20px;
  transition: all 0.3s ease-in-out;
  position: relative;

  &:hover {
    color: var(--violet-color);
    border: 1px solid var(--violet-color);
  }
}

// contacts
.contacts-list {
  display: flex;
  align-items: center;

  &__item {
    display: inline-flex;
    align-items: center;
    justify-content: center;

    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &__link {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
  }

  &__icon {
    flex-shrink: 0;
    width: 24px;
    height: 24px;
    fill: currentColor;
  }
}

/* ------------------- Adaptive -------------------- */
@media (max-width: 1280px) {
  .header__burger {
    display: inline-block;
  }

  .header__nav {
    width: 100%;
    height: 100vh;
    max-width: 320px;
    padding: 48px 32px;
    position: absolute;
    top: 85px;
    left: -100%;
    background: #170c1b;
    border-top: 1px solid rgba(#fff, 0.12);
    transition: left 0.3s ease-in-out;

    &.active {
      left: 0;
    }
  }

  .header__menu {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    .menu__item {
      width: 100%;

      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 32px;
      }
    }

    .menu__link {
      text-align: center;
      font-size: 18px;
      &.active {
        border: none;
        border-bottom: 1px solid #fff;
      }
    }
  }
}

@media (max-width: 860px) {
  .header {
    padding: 12px 0;

    &__container {
      padding: 0 15px;
    }
  }

  .header__left,
  .header__right {
    gap: 20px;
  }

  .header__nav {
    top: 79px;
  }

  .header .logo__img-lg {
    display: none;
  }

  .header .logo__img-mob {
    display: inline-block;
  }
}

@media (max-width: 480px) {
  .header__nav {
    max-width: 100%;
    padding: 24px;
    padding-top: 64px;
  }

  .header .menu__link {
    display: block;
    width: 100%;
  }

  .header__mob-contacts {
    align-items: center;
  }

  .header__mob-contacts .btn-submit-property {
    width: 100%;
  }

