$hero-height: 90vh;

.hero {
  width: 100%;
  height: $hero-height;
  min-height: 700px;
  position: relative;
  z-index: 0;

  &__inner-mob {
    width: 100%;
    min-height: 700px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    &::after {
      content: "";
      width: 100%;
      height: 100%;
      background: rgba(#0e0013, 0.7);
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: -1;
    }

    .hero__content {
      width: 100%;
      max-width: 960px;
      text-align: center;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &__inner {
    width: 100%;
    height: $hero-height;
    min-height: 700px;
    margin: auto;
    position: relative;

    // dots-slider
    .swiper-pagination {
      display: inline-flex;
      flex-direction: column;
      align-items: center;
      gap: 40px;
      width: 20px;
      position: absolute;
      left: inherit;
      bottom: inherit;
      top: 50%;
      right: 4%;
      transform: translateY(-50%);
    }

    .swiper-pagination-bullet {
      width: 8px;
      height: 8px;
      background: #d9d9d9;
    }

    .swiper-pagination-bullet-active {
      width: 10px;
      height: 10px;
      background: var(--violet-color);
      position: relative;
      transition: all 0.3s ease-in-out;

      &::before {
        display: inline-block;
        content: "";
        width: 24px;
        height: 24px;
        border-radius: 50%;
        border: 1px solid var(--violet-color);
        position: absolute;
        top: -7px;
        right: -7px;
        transition: all 0.3s ease-in-out;
      }
    }
  }

  .hero-slider {
    box-sizing: content-box;
    height: 100%;
  }

  &__slide {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    text-align: center;
    position: relative;

    &::after {
      content: "";
      width: 100%;
      height: 100%;
      background: rgba(#0e0013, 0.7);
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: -1;
    }

    &-img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
    }
  }

  &__content {
    width: 100%;
    max-width: 960px;
    text-align: center;
    padding: 0 24px;
    margin: 0 auto;
  }

  &__title {
    font-family: var(--font-title);
    // font-size: 90px;
    font-size: clamp(2rem, 0.596rem + 7.02vw, 5.625rem);
    font-weight: 400;
    line-height: 1.2;
    margin: 0;
    margin-bottom: 40px;
  }

  .btn-by {
    width: 100%;
    max-width: 280px;
  }

  .btn-down {
    display: inline-block;
    position: absolute;
    bottom: 80px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 100;

    .btn-arrow__icon {
      transition: top 0.2s ease-in-out;
    }

    &:hover .btn-arrow__icon {
      top: -20px;
    }
  }
}

// slider
.swiper-slide .hero__content {
  opacity: 0;
  transform: translateY(50px);
  transition: all 1s ease-in-out;
}

.swiper-slide-active .hero__content {
  opacity: 1;
  transform: translateY(0);
}

/* ------------------- Adaptive -------------------- */
@media (max-width: 1024px) {
  .hero__inner {
    display: none;
  }

  .hero-slider {
    display: none;

    .swiper-pagination {
      display: none;
    }
  }
}

@media (max-width: 600px) {
  .hero .btn-down {
    bottom: 90px;
  }
}
