.card-property {
  display: block;
  background: rgba(#8668e3, 0.1);
  border-bottom: 1px solid var(--violet-color);
  border-radius: 24px 24px 0px 0px;
  overflow: hidden;

  &__header {
    width: 100%;
    height: 290px;
    position: relative;
    overflow: hidden;
  }

  &__header-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
    position: relative;
    z-index: 1;

    .favorite-star {
      cursor: pointer;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 32px;
      height: 32px;
      background: var(--main-color);
      transition: all 0.3s ease-in-out;
      &:hover {
        opacity: 0.9;
      }
      svg {
        width: 18px;
        height: 18px;
        fill: #fff;
      }
    }
  }

  &__thumbnail {
    cursor: pointer;
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    transition: all 0.3s ease-in-out;
    overflow: hidden;

    &:hover {
      transform: scale(1.1);

      @media (max-width: 1024px) {
        transform: none;
      }
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__header-bottom {
    width: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
    padding: 16px;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
  }

  &__name {
    color: #fff;
    font-size: 16px;
    font-weight: 500;
  }

  &__body {
    padding: 24px;
  }

  &__title {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 10px;
  }

  &__location {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    margin-top: 10px;
  }

  &__location-icon {
    display: inline-block;
    width: 16px;
    height: 16px;
    fill: var(--violet-color);
    margin-right: 10px;
  }

  &__location-name {
    color: var(--violet-color);
    font-size: 14px;
    font-weight: 500;
  }

  &__apartments {
    margin-top: 24px;
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    gap: 16px;
    padding: 24px;
    padding-top: 0;
  }

  &__price {
    display: inline-block;
    font-size: 20px;
    font-weight: 600;
    background: var(--accent-gradient);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &__favorite {
    cursor: pointer;
    display: inline-block;
    width: 32px;
    height: 32px;
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover;
    transition: transform 0.3s ease-in-out;
    position: relative;

    &:hover .favorite-tooltip {
      left: -165px;
      opacity: 1;
      visibility: visible;
      pointer-events: all;
    }

    &:hover {
      color: inherit;
    }

    &.favorite-no {
      background-image: url("../img/icons/ic-favorite_no.svg");
    }
    &.favorite-yes {
      background-image: url("../img/icons/ic-favorite_yes.svg");
      opacity: 0.8;
    }

    .favorite-tooltip {
      cursor: default;
      display: inline-block;
      width: 150px;
      padding: 8px;
      background: var(--violet-color);
      border-radius: 6px;
      font-size: 14px;
      font-weight: 600;
      text-align: center;
      text-transform: uppercase;
      position: absolute;
      top: -6px;
      left: -155px;
      // display: none;
      transition: all 0.3s ease-in-out;
      animation: fade 0.3s ease-in-out;
      opacity: 0;
      visibility: hidden;
      pointer-events: none;

      &.active {
        display: inline-block;
        opacity: 1;
      }

      &::after {
        content: "";
        width: 0;
        height: 0;
        border-top: 8px solid transparent;
        border-left: 10px solid var(--violet-color);
        border-bottom: 8px solid transparent;
        position: absolute;
        top: 10px;
        right: -10px;
      }
      // transform: translate(-50%);
    }
  }
}

.gallery-triger {
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
  height: 32px;
  background: rgba(#220d2a, 0.6);
  border-radius: 2px;
  padding: 0px 7px;
  font-weight: 400;
  &:hover {
    color: #fff;
  }

  &__icon {
    width: 16px;
    height: 16px;
    fill: #fff;
  }
}

.property-apartments {
  display: flex;
  align-items: center;

  &__item {
    display: flex;
    &:not(:last-child) {
      margin-right: 24px;
    }
  }

  &__item-icon {
    flex-shrink: 0;
    display: inline-block;
    width: 24px;
    height: 24px;
    fill: #fff;
    margin-right: 16px;
  }
}
