.modal {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  position: fixed;
  top: 0;
  left: 0;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s ease-in-out;
  pointer-events: none;
  z-index: -9999999;

  &.active {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
    z-index: 9999999;

    .modal__content {
      transform: none;
    }
  }

  &__overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    padding: 20px 15px;
  }

  &__content {
    width: 100%;
    max-width: 560px;
    max-height: 90%;
    margin: auto;
    overflow: auto;
    background: #2e1538;
    padding: 32px;
    position: relative;
    transform: translateY(-200px);
    transition: transform 0.3s ease-in-out;

    @media (max-width: 480px) {
      transform: none;
    }
  }

  &__close {
    cursor: pointer;
    display: inline-block;
    width: 16px;
    height: 16px;
    background: url("data:image/svg+xml;base64,");
    background-size: cover;
    position: absolute;
    top: 20px;
    right: 20px;
    opacity: 0.3;
    transition: all 0.3s ease-in-out;
    &:hover {
      background: url("data:image/svg+xml;base64,");
      transform: rotate(180deg);
      opacity: 1;
    }
  }

  &__head {
    text-align: center;
    margin-bottom: 32px;
  }

  &__title {
    font-family: var(--font-title);
    font-size: 40px;
    font-weight: 400;
    margin-bottom: 10px;
  }

  &__descr {
    font-size: 18px;
  }
}

@media (max-width: 480px) {
  .modal__title {
    font-size: 32px;
  }

  .modal__content {
    padding: 32px 24px;
  }
}

@media (max-width: 375px) {
  .modal__head {
    margin-bottom: 24px;
  }

  .modal__descr {
    font-size: 16px;
  }

  .modal .check__label {
    font-size: 16px;
  }
}
