//     sass- -  root
// colors
:root {
  // Fonts
  --font-title: "Prata", sans-serif;
  --font-default: "FuturaNew", sans-serif;

  --content-width: 1146px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // Colos
  --body-bg-color: #220d2a;
  --light-color: #fff;
  --dark-color: #000;

  --main-color: #d32ac3;
  --main-gradient: linear-gradient(33deg, #d029c4 0%, #46226e 100%);
  --accent-color: #e7b400;
  --accent-gradient: linear-gradient(30deg, #c04804 0%, #ffc700 100%);
  --violet-color: #8668e3;
  --purple-color: #4f3a80;
  --light-blue_3: #73a5e3;
  --table-border_color: rgba(255, 255, 255, 0.4);
}
