.search-filter {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  margin-top: -60px;
  height: 120px;
  position: relative;
  z-index: 100;
  padding: 0 15px;

  &__wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: rgba(11, 11, 12, 0.4);
    backdrop-filter: blur(20px);
    border-bottom: 1px solid var(--violet-color);
    // border-radius: 24px 0 0 0;
    border-radius: 24px 24px 0 0;
    position: relative;
  }

  &__fields {
    width: 100%;
    gap: 32px;
    display: flex;
    flex-wrap: wrap;
    padding: 16px 32px;

    .c-dropdown,
    .search-filter__field {
      flex: 1 1 auto;
    }
  }

  &__btns {
    flex-shrink: 0;
    width: 100%;
    max-width: 240px;
    height: 100%;
    display: flex;
    align-items: center;
  }

  &__btn {
    flex-shrink: 0;
    cursor: pointer;
    width: 120px;
    height: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;

    &:hover svg {
      transform: scale(1.1);
    }

    svg {
      flex-shrink: 0;
      display: inline-block;
      width: 36px;
      height: 36px;
      fill: #fff;
      transition: all 0.2s ease-in-out;
    }
  }

  .btn-filter {
    background: var(--purple-color);

    &__icon {
      animation: fade 0.3s ease-in-out;

      &-close {
        width: 24px;
        height: 24px;
        display: none;
      }
    }

    &.active {
      .btn-filter__icon-filter {
        display: none;
      }

      .btn-filter__icon-close {
        display: inline-block;
      }
    }
  }

  .btn-search {
    background: var(--main-gradient);
    border-radius: 0 24px 0 0;
  }

  &__more-options {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 50;
    margin-top: 1px;
    //   
    transform-origin: top;
    transform: scaleY(0);
    visibility: hidden;
    pointer-events: none;
    transition: all 0.5s ease-in-out;

    &.open {
      transform: scaleY(1);
      visibility: visible;
      pointer-events: all;
    }
  }
}

// Filter Input
.search-filter__field {
  display: flex;
  flex-direction: column;
  gap: 10px;

  &-label {
    @extend .c-dropdown__label;
  }

  &-input {
    flex-shrink: 0;
    @extend .c-dropdown__search;

    border: none !important;
    padding: 0;
  }
}

// More Option
.more-options {
  width: 100%;
  max-width: 690px;
  background: #190b1e;
  border: 1px solid rgba(#fff, 0.05);
  padding: 32px;

  .search-filter__fields {
    padding: 0;
    gap: 32px;
  }

  .search-filter__field-label {
    margin-bottom: 0;
  }
}

// checkbox-options
.search-filter__checkbox-options {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  border-top: 1px solid rgba(#fff, 0.12);
  padding-top: 30px;
  margin: 20px 0;
  display: none;
  animation: fade 0.3s ease-in-out;

  &.open {
    display: flex;
    animation: fade 0.3s ease-in-out;

    .more-option__open-panel.active {
      margin-top: 10px;
    }
  }
}

.more-option__open-panel {
  cursor: pointer;
  display: flex;
  align-items: center;
  color: #fff;
  font-size: 18px;
  background: var(--purple-color);
  padding: 5px 15px;
  padding-left: 40px;
  margin-top: 20px;
  position: relative;

  &::before {
    display: inline-block;
    content: "";
    width: 14px;
    height: 14px;
    background: url("data:image/svg+xml;base64,")
      no-repeat center;
    background-size: cover;
    vertical-align: middle;
    position: absolute;
    top: 50%;
    left: 15px;
    transform: translateY(-50%);
    z-index: 0;
  }

  &.active {
    &::before {
      width: 16px;
      height: 2px;
      background: url("data:image/svg+xml;base64,")
        no-repeat center;
      background-size: cover;
    }
  }
}

/* ------------------- Adaptiv -------------------- */
@media (max-width: 800px) {
  .search-filter {
    width: 100%;
    max-width: 100%;
  }

  .search-filter__more-options {
    max-width: 100%;
  }

  .search-filter__fields {
    gap: 24px;
    padding: 16px 24px;
  }

  .search-filter__btn {
    border-radius: 0 !important;
  }
}

@media (max-width: 767px) {
  .search-filter {
    height: inherit;
  }

  .search-filter__wrapper {
    flex-direction: column;
    border: none;
  }

  .search-filter__fields {
    gap: 24px;
    padding: 24px;
  }

  .search-filter__btns {
    flex-shrink: inherit;
    width: 100%;
    max-width: 100%;
    height: 80px;
  }

  .search-filter__btn {
    flex-shrink: inherit;
    width: 100%;
    height: 100%;
  }

  .search-filter__fields {
    display: grid;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .more-options {
    padding: 24px 15px;
  }

  .search-filter__btns {
    height: 64px;
  }

  .search-filter__btn:hover {
    transform: none;
  }

  .search-filter__btn svg {
    width: 28px;
    height: 28px;
  }

  .search-filter .btn-filter__icon-close {
    height: 20px;
    width: 20px;
  }
}
