.show-content {
  display: flex;
  justify-content: center;
  padding-top: 30px;

  &__btn {
    height: 56px;
    color: #fff;
    background: transparent;
    border: 2px solid #fff;
    &::after {
      display: none;
    }

    &:hover {
      border-color: var(--main-color);
    }
  }

  @media (max-width: 1024px) {
    padding-top: 10px;
  }
}
