.bg-blur {
  position: relative;
  &::before {
    content: "";
    width: 406px;
    height: 406px;
    border-radius: 406px;
    opacity: 0.5;
    background: var(--violet-color, #8668e3);
    -webkit-filter: blur(217.5px);
    filter: blur(217.5px);
    position: absolute;
    top: 30%;
    left: -3%;
    -webkit-transform: translate(-50%);
    -ms-transform: translate(-50%);
    transform: translate(-50%);
  }

  &-top::before {
    top: -3%;
  }

  &-center::before {
    top: 50%;
  }

  &-bottom::before {
    top: -3%;
    left: -3%;
  }
}
