.news-post {
  &__container {
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
  }

  &__block {
    padding: 60px 0;
  }

  p {
    font-size: 20px;
    opacity: 0.85;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__heading {
    width: 100%;
    max-width: 960px;
    padding-bottom: 60px;
  }

  &__title {
    color: #fff;
    font-family: var(--font-title);
    font-size: 40px;
    font-weight: 400;
    line-height: 1.3;
    margin-bottom: 60px;
    position: relative;
    &::after {
      display: inline-block;
      content: "";
      width: 166px;
      height: 1px;
      background: var(--violet-color);
      position: absolute;
      bottom: -30px;
      left: 0;
    }
  }

  &__descr {
    font-size: 20px;
  }

  &__learn-more {
    padding: 60px 0;
    padding-top: 40px;
  }

  &__learn-more-card {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    border-radius: 0px 24px 24px 0px;
    border-left: 1px solid var(--violet-color);
    background: rgba(134, 104, 227, 0.2);
    border-radius: 0px 24px 24px 0px;
    overflow: hidden;
  }

  &__learn-more-text {
    padding: 30px;
    font-size: 20px;
  }

  &__learn-more-img {
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__read-more-link {
    cursor: pointer;
    display: inline-block;
    background: var(--accent-gradient);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    position: relative;
    transition: all 0.3s ease-in-out;

    &:hover::before {
      width: 0;
    }

    &::before {
      display: inline-block;
      content: "";
      width: 100%;
      height: 1px;
      background: var(--accent-gradient);
      position: absolute;
      bottom: 0;
      left: 0;
      transition: width 0.3s ease-in-out;
    }
  }

  &__learn-more-img {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__section {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 60px 0;
    position: relative;
    z-index: 0;
  }

  &__section-img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    opacity: 0.2;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__list {
    list-style: none;
    font-size: 20px;
    line-height: 1.4;
    margin-bottom: 60px;
  }

  &__list-item {
    display: flex;
    &:not(:last-child) {
      margin-bottom: 40px;
    }

    &-icon {
      flex-shrink: 0;
      display: inline-block;
      width: 66px;
      height: 59px;
      background: url("../img/icons/ic-check_list.svg") no-repeat center;
      background-size: cover;
      margin-right: 30px;
    }
  }

  &__h3 {
    font-size: clamp(1.5rem, 0.929rem + 2.86vw, 2rem);
    font-weight: 400;
    line-height: 1.2;
    &:not(:last-child) {
      margin: 30px 0;
    }
  }

  &__h4 {
    font-size: clamp(1.125rem, 0.696rem + 2.14vw, 1.5rem);
    font-weight: 500;
    margin: 20px 0;
  }

  &__contacts-us {
    display: flex;
    justify-content: center;
    padding-top: 50px;
  }
}

.btn-contacts-us {
  margin: auto;
}

@media (max-width: 767px) {
  .news-post__learn-more-card {
    grid-template-columns: 1fr;
    border-radius: 24px 24px 0 0;
    border-left: none;
    border-bottom: 1px solid var(--violet-color);
  }

  .news-post__learn-more-img {
    order: -1;
    height: 320px;
  }
}

@media (max-width: 480px) {
  .news-post__title {
    font-size: 32px;
  }

  .news-post p,
  .news-post__list {
    font-size: 16px;
  }

  .news-post__descr {
    font-size: 18px !important;
  }

  .news-post__learn-more-text {
    padding: 20px;
    font-size: 18px;
  }

  .news-post__section,
  .news-post__learn-more {
    padding: 40px 0;
    padding-top: 20px;

    .news-post__h3 {
      font-size: 20px;
      line-height: 1.3;
    }
  }

  .news-post__bg-section {
    padding: 40px 0;
  }

  .news-post__learn-more-img {
    height: 200px;
  }

  .news-post__block {
    padding: 40px 0;
  }

  .news-post__list-item {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  .news-post__list-item-icon {
    width: 45px;
    height: 40px;
    margin-right: 20px;
  }

  .news-post__contacts-us {
    padding-top: 20px;
  }

  .news-post__h3:not(:last-child) {
    margin: 20px 0;
  }
}
