.ic-currency {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &.ic-btc {
    background-image: url("../img/icons/ic-btc.svg");
  }

  &.ic-eth {
    background-image: url("../img/icons/ic-eth.svg");
  }

  &.ic-tether {
    background-image: url("../img/icons/ic-tether.svg");
  }

  &.ic-usd {
    background-image: url("../img/icons/ic-usd.svg");
  }
}

.ic-arrow-down {
  display: inline-block;
  width: 44px;
  height: 64px;
  background: url("../img/icons/ic-arrow_down.svg") no-repeat center;
  background-size: cover;
}

.ic-arrow-up {
  display: inline-block;
  width: 44px;
  height: 64px;
  background: url("../img/icons/ic-arrow_up.svg") no-repeat center;
  background-size: cover;
}
