html {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-style: normal;
}

.page {
  height: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  color: #fff;
  background: var(--body-bg-color);
  font-family: var(--font-default, sans-serif);
  font-size: 18px;
  font-weight: 400;
  line-height: 1.4;
  overflow-x: hidden;
}
.page__body.lock-scroll {
  overflow: hidden;
}

img {
  display: inline-block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
  vertical-align: middle;
}

a {
  color: inherit;
  text-decoration: none;
  transition: color 0.2s ease-in-out;
}
a:hover {
  color: var(--main-color);
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  width: 100%;
  margin: 0 auto;
  max-width: var(--container-width);
  padding: 0 15px;
}
@media (min-width: 480px) {
  .container {
    padding: 0 24px;
  }
}
@media (min-width: 768px) {
  .container {
    padding: 0 32px;
  }
}
@media (min-width: 1024px) {
  .container {
    padding: 0 48px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.hidden {
  display: none;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.text-accent {
  color: var(--violet-color);
}

.text-center {
  text-align: center;
}

.section {
  padding: clamp(2rem, 1.08rem + 4.6vw, 4.375rem) 0;
}

.section-head {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  text-align: center;
  margin-bottom: clamp(2rem, 0.838rem + 5.81vw, 5rem);
}
.section-head .section-title {
  margin-bottom: 30px;
}

.section-title {
  font-family: var(--font-title);
  font-size: clamp(1.5rem, 1.322rem + 3.39vw, 3.75rem);
  font-weight: 400;
  line-height: 1.3;
  text-align: center;
  text-transform: Capitalize;
  position: relative;
}
.section-title::after {
  content: "";
  width: 100%;
  max-width: 40%;
  height: 1px;
  background: var(--violet-color);
  position: absolute;
  bottom: -3px;
  left: 50%;
  transform: translateX(-50%);
}

.section-descr {
  font-family: var(--font-default);
  font-size: 20px;
  font-weight: 400;
  line-height: 1.5;
  text-align: center;
}
@media (max-width: 480px) {
  .section-descr {
    font-size: 18px;
  }
}

.item-title {
  font-family: var(--font-title);
  font-size: 24px;
  font-weight: 400;
  line-height: 1.3;
  margin-bottom: 20px;
}

.item-text {
  font-size: 18px;
  line-height: 1.4;
  font-weight: 400;
  opacity: 0.85;
}
@media (max-width: 360px) {
  .item-text {
    font-size: 16px;
  }
}

.page-preloader {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: var(--body-bg-color);
  z-index: 999999999999;
}
.page-preloader.hide {
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s ease, visibility 1s;
}

.loader {
  width: 80px;
  height: 80px;
  border: 3px dotted #fff;
  border-style: solid solid dotted dotted;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  animation: rotation 2s linear infinite;
}
.loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  border: 3px dotted var(--main-color);
  border-style: solid solid dotted;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  animation: rotationBack 1s linear infinite;
  transform-origin: center center;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}
.bg-blur {
  position: relative;
}
.bg-blur::before {
  content: "";
  width: 406px;
  height: 406px;
  border-radius: 406px;
  opacity: 0.5;
  background: var(--violet-color, #8668e3);
  -webkit-filter: blur(217.5px);
  filter: blur(217.5px);
  position: absolute;
  top: 30%;
  left: -3%;
  -webkit-transform: translate(-50%);
  -ms-transform: translate(-50%);
  transform: translate(-50%);
}
.bg-blur-top::before {
  top: -3%;
}
.bg-blur-center::before {
  top: 50%;
}
.bg-blur-bottom::before {
  top: -3%;
  left: -3%;
}

.search-filter {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  margin-top: -60px;
  height: 120px;
  position: relative;
  z-index: 100;
  padding: 0 15px;
}
.search-filter__wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(11, 11, 12, 0.4);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid var(--violet-color);
  border-radius: 24px 24px 0 0;
  position: relative;
}
.search-filter__fields {
  width: 100%;
  gap: 32px;
  display: flex;
  flex-wrap: wrap;
  padding: 16px 32px;
}
.search-filter__fields .c-dropdown,
.search-filter__fields .search-filter__field {
  flex: 1 1 auto;
}
.search-filter__btns {
  flex-shrink: 0;
  width: 100%;
  max-width: 240px;
  height: 100%;
  display: flex;
  align-items: center;
}
.search-filter__btn {
  flex-shrink: 0;
  cursor: pointer;
  width: 120px;
  height: 100%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.search-filter__btn:hover svg {
  transform: scale(1.1);
}
.search-filter__btn svg {
  flex-shrink: 0;
  display: inline-block;
  width: 36px;
  height: 36px;
  fill: #fff;
  transition: all 0.2s ease-in-out;
}
.search-filter .btn-filter {
  background: var(--purple-color);
}
.search-filter .btn-filter__icon {
  animation: fade 0.3s ease-in-out;
}
.search-filter .btn-filter__icon-close {
  width: 24px;
  height: 24px;
  display: none;
}
.search-filter .btn-filter.active .btn-filter__icon-filter {
  display: none;
}
.search-filter .btn-filter.active .btn-filter__icon-close {
  display: inline-block;
}
.search-filter .btn-search {
  background: var(--main-gradient);
  border-radius: 0 24px 0 0;
}
.search-filter__more-options {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 50;
  margin-top: 1px;
  transform-origin: top;
  transform: scaleY(0);
  visibility: hidden;
  pointer-events: none;
  transition: all 0.5s ease-in-out;
}
.search-filter__more-options.open {
  transform: scaleY(1);
  visibility: visible;
  pointer-events: all;
}

.search-filter__field {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.search-filter__field-input {
  flex-shrink: 0;
  border: none !important;
  padding: 0;
}

.more-options {
  width: 100%;
  max-width: 690px;
  background: #190b1e;
  border: 1px solid rgba(255, 255, 255, 0.05);
  padding: 32px;
}
.more-options .search-filter__fields {
  padding: 0;
  gap: 32px;
}
.more-options .search-filter__field-label {
  margin-bottom: 0;
}

.search-filter__checkbox-options {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  border-top: 1px solid rgba(255, 255, 255, 0.12);
  padding-top: 30px;
  margin: 20px 0;
  display: none;
  animation: fade 0.3s ease-in-out;
}
.search-filter__checkbox-options.open {
  display: flex;
  animation: fade 0.3s ease-in-out;
}
.search-filter__checkbox-options.open .more-option__open-panel.active {
  margin-top: 10px;
}

.more-option__open-panel {
  cursor: pointer;
  display: flex;
  align-items: center;
  color: #fff;
  font-size: 18px;
  background: var(--purple-color);
  padding: 5px 15px;
  padding-left: 40px;
  margin-top: 20px;
  position: relative;
}
.more-option__open-panel::before {
  display: inline-block;
  content: "";
  width: 14px;
  height: 14px;
  background: url("data:image/svg+xml;base64,") no-repeat center;
  background-size: cover;
  vertical-align: middle;
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  z-index: 0;
}
.more-option__open-panel.active::before {
  width: 16px;
  height: 2px;
  background: url("data:image/svg+xml;base64,") no-repeat center;
  background-size: cover;
}

/* ------------------- Adaptiv -------------------- */
@media (max-width: 800px) {
  .search-filter {
    width: 100%;
    max-width: 100%;
  }
  .search-filter__more-options {
    max-width: 100%;
  }
  .search-filter__fields {
    gap: 24px;
    padding: 16px 24px;
  }
  .search-filter__btn {
    border-radius: 0 !important;
  }
}
@media (max-width: 767px) {
  .search-filter {
    height: inherit;
  }
  .search-filter__wrapper {
    flex-direction: column;
    border: none;
  }
  .search-filter__fields {
    gap: 24px;
    padding: 24px;
  }
  .search-filter__btns {
    flex-shrink: inherit;
    width: 100%;
    max-width: 100%;
    height: 80px;
  }
  .search-filter__btn {
    flex-shrink: inherit;
    width: 100%;
    height: 100%;
  }
  .search-filter__fields {
    display: grid;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 480px) {
  .more-options {
    padding: 24px 15px;
  }
  .search-filter__btns {
    height: 64px;
  }
  .search-filter__btn:hover {
    transform: none;
  }
  .search-filter__btn svg {
    width: 28px;
    height: 28px;
  }
  .search-filter .btn-filter__icon-close {
    height: 20px;
    width: 20px;
  }
}
