.btn-burger {
  cursor: pointer;
  width: 40px;
  height: 40px;
  background: transparent;
  outline: none;
  border: none;
  position: relative;
  transition: all 0.4s ease-in-out;
}
.btn-burger__line {
  width: 100%;
  height: 4px;
  background: var(--main-gradient);
  position: absolute;
  top: 50%;
  left: 0;
  transition: all 0.2s ease-in-out;
}
.btn-burger__line::before, .btn-burger__line::after {
  content: "";
  width: 100%;
  height: 4px;
  background: var(--main-gradient);
  position: absolute;
  right: 0;
  opacity: 1;
  transition: transform 0.2s ease-in-out;
}
.btn-burger__line::before {
  width: 40px;
  top: -10px;
}
.btn-burger__line::after {
  width: 24px;
  bottom: -10px;
}
.btn-burger.active .btn-burger__line {
  background: transparent;
}
.btn-burger.active .btn-burger__line::before {
  top: 20%;
  transform: rotate(45deg);
}
.btn-burger.active .btn-burger__line::after {
  width: 40px;
  top: 0;
  bottom: 50%;
  transform: rotate(-45deg);
}

