.card-news {
  @extend .card-property;

  &__thumbnail {
    display: block;
    height: 200px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.3s ease-in-out;
    }

    &:hover img {
      transform: scale(1.1);

      @media (max-width: 1024px) {
        transform: none;
      }
    }
  }

  &__body {
    padding: 24px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px 0;
    padding-top: 0;
  }

  &__info {
    display: inline-flex;
    align-items: center;
    gap: 10px;
    color: var(--violet-color);
    font-weight: 500;
  }

  &__info-icon {
    display: inline-block;
    width: 18px;
    height: 18px;
    fill: var(--violet-color);
  }

  &__title {
    cursor: pointer;
    display: block;
    &:hover {
      text-decoration: none;
      // color: #fff;
    }
  }

  &__title-h3 {
    @extend .card-property__title;
    max-height: 3em;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2; /*    */
    overflow: hidden;
  }

  &__read-more {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    gap: 10px;
    background: var(--accent-gradient);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;
    transition: all 0.3s ease-in-out;
    margin-top: 28px;

    .ic-readmore {
      position: relative;
      right: 0;
      transition: all 0.3s ease-in-out;
    }

    &:hover .ic-readmore {
      right: -4px;
    }
  }
}

.ic-readmore {
  display: inline-block;
  width: 24px;
  height: 24px;
  background: url("../img/icons/ic_read-more.png") no-repeat center;
  background-size: cover;
}
