.steps {
  padding-top: clamp(3.75rem, 1.813rem + 9.69vw, 8.75rem);
  position: relative;

  &__container {
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
  }

  &__head {
    text-align: center;
  }

  &__title::after {
    display: none;
  }

  .steps-list {
    width: 100%;
    max-width: 900px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    position: relative;
    left: -80px;
    position: relative;

    &__line {
      width: 4px;
      height: 100%;
      background: linear-gradient(180deg, #68c5e3 0%, #8668e3 100%);
      position: absolute;
      top: 0;
      left: 488px;
      &.step-line-anim {
        height: 0;
        opacity: 0;
        transition: height 6s ease, opacity 0.3s ease;
      }
    }

    &__item {
      display: flex;
      align-items: center;
      gap: 180px;
      position: relative;

      &.step-1,
      &.step-3,
      &.step-5,
      &.step-7 {
        align-self: flex-end;
        right: -33.5px;

        .steps-list__item-icon {
          &::after {
            right: -70px;
          }
        }

        .steps-list__item-body {
          border-radius: 0 24px 24px 0;
          border-left: 1px solid var(--light-blue_3);

          &::before {
            background-image: url("../img/step-before__left.svg");
            left: -99px;
          }
        }

        .steps-list__item-text {
          border-radius: 0 24px 24px 0;
        }
      }

      &.step-2,
      &.step-4,
      &.step-6 {
        align-self: flex-start;
        right: -80px;

        .steps-list__item-icon {
          &::after {
            left: -70px;
          }
        }

        .steps-list__item-body {
          border-radius: 24px 0 0 24px;
          border-right: 1px solid var(--light-blue_3);

          &::before {
            background-image: url("../img/step-before__right.svg");
            right: -100px;
          }
        }

        .steps-list__item-text {
          border-radius: 24px 0 0 24px;
        }
      }
    }

    &__item-icon {
      flex-shrink: 0;
      display: inline-block;
      width: 120px;
      height: 120px;
      position: relative;
      &::after {
        display: inline-block;
        content: "";
        width: 60px;
        height: 1px;
        background: var(--light-blue_3);
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    &__item-body {
      width: 100%;
      max-width: 320px;
      background: rgba(#8668e3, 0.1);
      display: flex;
      align-items: center;
      position: relative;
      &::before {
        display: inline-block;
        content: "";
        width: 90px;
        height: 20px;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: cover;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 0;
      }
    }

    &__item-text {
      background: rgba(#8668e3, 0.1);
      padding: 16px;
      font-size: 20px;
      line-height: 1.5;
    }

    &__item-title {
      font-size: 20px;
      font-weight: 500;
      margin-bottom: 5px;
    }

    &__item-descr {
      font-size: 18px;
    }

    &__item-number {
      color: var(--light-blue_3);
      font-size: 40px;
      font-weight: 300;
      padding: 16px;
    }

    &__number-mob {
      flex-shrink: 0;
      display: none;
      align-items: center;
      justify-content: center;
      width: 48px;
      height: 48px;
      color: var(--light-blue_3);
      border: 1px solid var(--light-blue_3);
      border-radius: 50%;
      font-size: 18px;
      font-weight: 500;
      position: relative;
      &::after {
        content: "";
        width: 32px;
        height: 1px;
        background: var(--light-blue_3);
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: -32px;
      }

      @media (max-width: 480px) {
        display: inline-flex;
      }
    }
  }

  &__text {
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
    color: var(--violet-color);
    font-family: var(--font-title);
    font-size: 24px;
    font-weight: 400;
    line-height: 1.5;
    text-align: center;
    margin-top: 80px;
  }
}

/* ------------------- Adaptive -------------------- */
@media (max-width: 1024px) {
  .steps .steps-list {
    left: -40px;
  }
}

@media (max-width: 1023px) {
  .steps .steps-list {
    left: 0px;
    flex-direction: column;
    gap: 32px;
    align-items: center;
  }

  .steps .steps-list::before {
    left: 0;
    display: none;
  }

  .steps .steps-list__line {
    left: 0;
    display: none;
  }

  .steps .steps-list__item {
    gap: 113px;
    align-self: center !important;
  }

  .steps .steps-list__item.step-1,
  .steps .steps-list__item.step-3,
  .steps .steps-list__item.step-5,
  .steps .steps-list__item.step-7 {
    align-self: flex-start;
  }

  .steps .steps-list__item.step-1 .steps-list__item-icon::after,
  .steps .steps-list__item.step-3 .steps-list__item-icon::after,
  .steps .steps-list__item.step-5 .steps-list__item-icon::after,
  .steps .steps-list__item.step-7 .steps-list__item-icon::after {
    display: none;
  }

  .steps .steps-list__item.step-2,
  .steps .steps-list__item.step-4,
  .steps .steps-list__item.step-6 {
    right: inherit;
  }

  .steps .steps-list__item.step-2,
  .steps .steps-list__item.step-4,
  .steps .steps-list__item.step-6 {
    .steps-list__item-number {
      order: 2;
    }
  }

  .steps .steps-list__item.step-2 .steps-list__item-body,
  .steps .steps-list__item.step-4 .steps-list__item-body,
  .steps .steps-list__item.step-6 .steps-list__item-body {
    order: 2;
    border-radius: 0 24px 24px 0;
    border-right: 1px solid transparent;
    border-left: 1px solid var(--light-blue_3);
  }

  .steps .steps-list__item.step-2 .steps-list__item-text,
  .steps .steps-list__item.step-4 .steps-list__item-text,
  .steps .steps-list__item.step-6 .steps-list__item-text {
    border-radius: 0;
  }

  .steps .steps-list__item.step-2 .steps-list__item-icon::after,
  .steps .steps-list__item.step-4 .steps-list__item-icon::after,
  .steps .steps-list__item.step-6 .steps-list__item-icon::after {
    display: none;
  }

  .steps .steps-list__item.step-2 .steps-list__item-body::before,
  .steps .steps-list__item.step-4 .steps-list__item-body::before,
  .steps .steps-list__item.step-6 .steps-list__item-body::before {
    right: inherit;
    left: -100px;
    background-image: url("../img/step-before__left.svg");
  }

  .steps .steps-list__item.step-1,
  .steps .steps-list__item.step-3,
  .steps .steps-list__item.step-5,
  .steps .steps-list__item.step-7 {
    right: 0;
  }
}

@media (max-width: 600px) {
  .steps .steps-list__item {
    gap: 80px;
  }

  .steps .steps-list__item-descr {
    font-size: 18px;
    line-height: 24px;
  }

  .steps .steps-list__item-icon {
    width: 96px;
    height: 96px;
  }

  .steps .steps-list__item.step-1 .steps-list__item-body::before,
  .steps .steps-list__item.step-3 .steps-list__item-body::before,
  .steps .steps-list__item.step-5 .steps-list__item-body::before,
  .steps .steps-list__item.step-7 .steps-list__item-body::before {
    width: 70px;
    left: -70px;
  }

  .steps .steps-list__item.step-2 .steps-list__item-body::before,
  .steps .steps-list__item.step-4 .steps-list__item-body::before,
  .steps .steps-list__item.step-6 .steps-list__item-body::before {
    width: 70px;
    left: -70px;
  }
}

@media (max-width: 480px) {
  .steps__text {
    margin-top: 60px;
  }

  .steps-list__item {
    gap: 32px !important;
  }

  .steps-list__item-number {
    display: none !important;
  }

  .steps-list__item-text,
  .steps-list__item-body {
    border-radius: 0 16px 16px 0 !important;
  }

  .steps .steps-list__item-icon {
    display: none;
  }

  .steps .steps-list__item.step-2 .steps-list__item-body::before,
  .steps .steps-list__item.step-4 .steps-list__item-body::before,
  .steps .steps-list__item.step-6 .steps-list__item-body::before {
    display: none;
  }

  .steps .steps-list__item.step-1 .steps-list__item-body::before,
  .steps .steps-list__item.step-3 .steps-list__item-body::before,
  .steps .steps-list__item.step-5 .steps-list__item-body::before,
  .steps .steps-list__item.step-7 .steps-list__item-body::before {
    display: none;
  }
}
