.footer {
  padding-top: 70px;

  &__container {
    padding: 70px 15px;
    border-top: 1px solid var(--violet-color);
    position: relative;
  }

  &__btn-up {
    cursor: pointer;
    display: inline-block;
    width: 74px;
    height: 93px;
    background: url("../img/icons/btn-up_footer.svg") no-repeat center;
    background-size: cover;
    position: absolute;
    top: -93px;
    left: 50%;
    transform: translateX(-50%);
  }

  &__grid {
    display: grid;
    grid-template-columns: 5fr 2fr 2fr 3fr;
    gap: 30px;
  }

  &__info-top {
    display: flex;
    align-items: center;
    gap: 30px;
    margin-bottom: 20px;
  }

  &__logo {
    flex-shrink: 0;
    height: 78px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__text {
    font-size: 14px;
    font-weight: 400;

    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &__email {
    display: inline-flex;
    align-items: center;
    font-size: 20px;
    font-weight: 400;
    line-height: 1;
    margin-top: 20px;

    &:hover {
      text-decoration: underline;
    }

    &:hover .footer__email-icon {
      fill: #fff;
      text-decoration: none;
    }
  }

  &__email-icon {
    display: inline-block;
    width: 24px;
    height: 24px;
    fill: #fff;
    margin-right: 10px;
  }

  &__title {
    font-size: 24px;
    font-weight: 300;
    text-transform: uppercase;
    margin-bottom: 60px;
  }
}

.footer-social {
  display: flex;
  align-items: center;
  gap: 30px;

  &__item {
    display: inline-block;
  }

  &__icon {
    display: inline-block;
    width: 24px;
    height: 24px;
    fill: currentColor;
  }
}

.footer-menu {
  display: flex;
  flex-direction: column;

  &__item {
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__link {
    display: inline-block;
  }
}

.crypto-list {
  display: flex;
  flex-direction: column;

  &__item {
    display: inline-flex;
    align-items: center;

    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &__item-icon {
    flex-shrink: 0;
    margin-right: 10px;
  }

  &__item-info {
    font-weight: 600;
  }

  &__item-name {
    display: inline-block;
    opacity: 0.5;
    margin-right: 10px;
  }
}

@media (max-width: 1124px) {
  .footer__grid {
    gap: 16px;
  }

  .footer__logo .logo__img-lg {
    display: none;
  }

  .footer__logo .logo__img-mob {
    width: 80px;
    height: 80px;
    flex-shrink: 0;
    display: inline-block;
  }
}

@media (max-width: 992px) {
  .footer__grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 100px;
    row-gap: 32px;
  }

  .footer__title {
    margin-bottom: 24px;
  }
}

@media (max-width: 600px) {
  .footer__grid {
    gap: 32px;
    row-gap: 32px;
  }
}

@media (max-width: 567px) {
  .footer__grid {
    grid-template-columns: 1fr;
  }

  .footer__info-top {
    flex-direction: column;
  }

  .footer__location {
    display: none !important;
  }
}

@media (max-width: 480px) {
  .footer__container {
    padding: 40px 15px;
  }

  .footer__column {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border-top: 1px solid rgba(#fff, 0.1);
    padding-top: 24px;

    &.footer__info {
      border: none;
    }
  }

  .footer .footer__expore {
    order: 3;
  }
}
