$btn-radius: 0px;

.btn {
  outline: none;
  border: none;
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 64px;
  line-height: 64px;
  padding: 0 30px;
  position: relative;
  z-index: 0;

  color: #fff;
  background: var(--main-gradient);
  border-radius: $btn-radius;

  font-size: 24px;
  font-weight: 500;
  text-transform: uppercase;
  transition: all 0.3s ease-in-out;

  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: #fff;
    text-decoration: none;
    background: linear-gradient(33deg, #46226e 0%, #d029c4 100%);
    border-radius: $btn-radius;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    z-index: -1;
  }

  &:hover {
    color: #fff;
  }

  &:hover:after {
    opacity: 1;
  }

  &-stroke {
    color: #fff;
    background: transparent;
    border: 2px solid #fff;
    &::after {
      display: none;
    }

    &:hover {
      border-color: var(--main-color);
    }
  }
}

@media (max-width: 480px) {
  .btn {
    width: 100%;
    height: 56px;
    line-height: 56px;
    padding: 0 24px;
    font-size: 20px;
  }
}
