.property-map {
  width: 100%;
  height: 300px;

  &.is-mapBg {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;
    .property-map__icon {
      position: absolute;
      top: 30%;
      left: 50%;
      transform: translate(-50%);
    }
  }
}
