.product {
  padding-top: 80px;

  .product__heading {
    width: 100%;
    max-width: 1060px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    flex-wrap: wrap;
    padding: 0 15px;
  }

  .product__title {
    font-family: var(--font-title);
    font-size: clamp(1.75rem, 1.406rem + 1.53vw, 2.5rem);
    font-weight: 400;
    line-height: 1.4;
  }

  .product__price {
    background: var(--accent-gradient);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 40px;
    font-weight: 600;
    line-height: 1;
  }

  .product__location {
    @extend .card-property__location;
    padding-left: 15px;
    margin-top: 20px;

    &-icon {
      @extend .card-property__location-icon;
      margin-right: 12px;
    }

    &-name {
      @extend .card-property__location-name;
      font-size: 20px;
      font-weight: 400;
      @media (max-width: 480px) {
        font-size: 18px;
      }
    }
  }

  &__apartments {
    padding: 0 15px;
    margin-top: 50px;

    .property-apartments__item {
      font-size: 24px;
      align-items: end;
    }

    .property-apartments__item:not(:last-child) {
      margin-right: 30px;
    }

    .property-apartments__item-icon {
      width: 48px;
      height: 48px;
      fill: var(--main-color);
    }
  }
}

// Product Slider
.slider-product {
  width: 100%;
  max-width: var(--content-width);
  box-sizing: border-box;
  padding-top: 60px;

  &__container {
    width: 100%;
    max-width: var(--content-width);
    overflow: hidden;
    box-sizing: border-box;
    max-height: 500px;
  }

  .swiper-slide {
    padding: 0 15px;
  }

  .swiper-slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 24px 24px 0 0;
  }

  &__thumbnails {
    width: 100%;
    max-width: var(--content-width);
    display: flex;
    justify-content: center;
    flex-wrap: nowrap;
    justify-content: space-between;
    margin: 0 auto;
    margin-top: 30px;
    overflow: hidden;
  }

  .swiper-thumbnail {
    cursor: pointer;
    padding: 0 15px;
    opacity: 0.6;
    transition: opacity 0.3s ease-in-out;

    &:hover,
    &.active {
      opacity: 1;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  .slider-btns {
    display: flex;
    justify-content: center;
    padding-top: 60px;
  }
}

// Product Info
.product-info {
  padding-bottom: 0;
  &__container {
    display: grid;
    grid-template-columns: 60% auto;
    gap: 30px;
  }

  &__title {
    font-family: var(--font-title);
    font-size: 28px;
    font-weight: 400;
    line-height: 1.4;
    margin-bottom: 32px;
  }

  &__block {
    padding-bottom: 70px;
  }

  .contact-form {
    width: 100%;
    max-width: 360px;
    margin-left: auto;
    position: sticky;
    top: 30px;
    right: 0;
  }
}

// Features
.features-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 40px;
  row-gap: 20px;

  &__item {
    display: flex;
    align-items: center;

    &-icon {
      flex-shrink: 0;
      width: 24px;
      height: 24px;
      margin-right: 12px;
    }

    &-label {
      font-size: 18px;
    }
  }
}

// Description
.product-descr .product-info__title {
  margin-bottom: 20px;
}
.product-descr {
  &__text {
    font-size: 18px;
  }
}

// Overview
.product-overview {
  padding-bottom: 0;
}

.product-overview .product-info__title {
  margin-bottom: 20px;
}

.overview-table {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  font-size: 18px;

  &__column .overview-table__item {
    &:first-child {
      border-top: 1px solid var(--table-border_color);
    }
  }

  &__column.column-1 .overview-table__item {
    border-right: none;
  }

  &__item {
    display: grid;
    grid-template-columns: 45% auto;
    padding: 10px 15px;
    border: 1px solid var(--table-border_color);
    border-top: none;
  }

  &__item-data {
    color: #6ac0e3;
  }
}

// Semilar Properties
.semilar-properties {
  padding-top: 70px;
  padding-bottom: clamp(3.75rem, 1.813rem + 9.69vw, 8.75rem);
  @media (max-width: 480px) {
    padding-top: 20px;
  }
}

/* ------------------- Adaptive -------------------- */
@media (max-width: 1024px) {
  .features-list {
    gap: 20px !important;
  }

  .features-list__item-label {
    font-size: 16px;
  }

  .overview-table__item {
    grid-template-columns: 40% auto;
    font-size: 16px;
  }
}

@media (max-width: 992px) {
  .product-info__container {
    grid-template-columns: 1fr;
  }

  .product-info .contact-form {
    width: 100%;
    max-width: 480px;
    position: relative;
    top: inherit;
    right: inherit;
    margin-left: inherit;
    margin: 0 auto;
  }
}

@media (max-width: 600px) {
  .product {
    padding-top: 60px;
  }

  .product .product__price {
    font-size: 32px;
  }

  .slider-product__thumbnails {
    margin-top: 20px;
  }

  .slider-product .swiper-thumbnail,
  .slider-product .swiper-slide {
    padding: 0 5px;
  }

  .product-info__block {
    padding-bottom: 60px;
  }

  .product-overview {
    padding-bottom: 30px;
  }

  .product-info__title {
    font-size: 24px;
  }

  .features-list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .slider-product,
  .slider-product .slider-btns {
    padding-top: 40px;
  }

  .product-info__container {
    gap: 0;
  }

  .product-descr__text {
    font-size: 16px;
  }

  .overview-table {
    grid-template-columns: 1fr;
  }

  .overview-table__column {
    &.column-2 {
      .overview-table__item:first-child {
        border-top: none;
      }
    }
  }

  .overview-table__column.column-1 .overview-table__item {
    border-right: 1px solid var(--table-border_color);
  }

  .product__apartments {
    margin-top: 20px;
  }

  .product__apartments .property-apartments__item-icon {
    width: 32px;
    height: 32px;
  }

  .product__apartments .property-apartments__item {
    font-size: 18px;
  }
}

@media (max-width: 375px) {
  .features-list {
    grid-template-columns: 1fr;
  }
}
