.locations {
  &__list {
    height: 476px;
    display: grid;
    grid-template-columns: 9fr 3fr;
    gap: 30px;
  }

  &__left {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    gap: 30px;
  }

  &__left-top {
    display: grid;
    grid-template-columns: 5fr 4fr;
    gap: 30px;

    .location__img {
      height: 223px;
    }
  }

  &__left-bottom {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;

    .location__img {
      height: 223px;
    }
  }
}

.location {
  &__item {
    cursor: pointer;
    display: block;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    text-decoration: none;
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease-in-out;

    &:hover {
      color: #fff;
    }

    &:hover .location__item-overlay {
      opacity: 1;
    }

    &:hover .location__name {
      top: 20px;
    }

    &:hover .location__properties {
      bottom: 20px;
    }
  }

  &__item-overlay {
    width: 100%;
    height: 100%;
    background: rgba(#0e0013, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: opacity 0.3s ease-in-out;
    opacity: 0;
  }

  &__name {
    display: inline-block;
    position: absolute;
    top: -100%;
    left: 20px;
    font-size: 20px;
    font-weight: 600;
    transition: top 0.3s ease-in-out;
  }

  &__properties {
    display: inline-block;
    position: absolute;
    bottom: -100%;
    right: 20px;
    font-size: 20px;
    font-weight: 700;
    transition: bottom 0.3s ease-in-out;
  }

  &__properties-count {
    font-weight: 600;
  }
}

@media (max-width: 1200px) {
  .locations__list {
    grid-template-columns: 8fr 4fr;
    gap: 16px;
  }

  .locations__left-top {
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
  }

  .locations__left-bottom {
    gap: 16px;
  }

  .locations__left {
    gap: 16px;
  }
}

@media (max-width: 1024px) {
  .location__item-overlay {
    opacity: 1 !important;
    background: rgba(14, 0, 19, 0.5);
  }

  .location__name {
    top: 20px !important;
  }

  .location__properties {
    bottom: 20px !important;
  }
}

@media (max-width: 992px) {
  .locations__list {
    height: 360px;
  }
}

@media (max-width: 767px) {
  .locations {
    padding-bottom: 70px;
  }

  .locations__list {
    height: inherit;

    grid-template-columns: 1fr;
  }

  .locations__left {
    grid-template-rows: auto;
  }

  .locations__left-bottom,
  .locations__left-top {
    grid-template-columns: 1fr;
  }

  .location__item {
    height: 360px;
  }
}

@media (max-width: 480px) {
  .location__item {
    height: 160px;
  }
}
