.why-choose {
  position: relative;
  padding: 0 15px;

  &__container {
    width: 100%;
    border-radius: 24px 24px 0px 0px;
    background: linear-gradient(
      136deg,
      rgba(208, 41, 196, 0.1) 0%,
      rgba(70, 34, 110, 0.1) 100%
    );
    border-bottom: 1px solid #7c248f;
    padding: 80px 40px;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
  }

  &__item {
    display: flex;
    align-items: center;
  }

  &__item-img {
    flex-shrink: 0;
    margin-right: 30px;
    img {
      border-radius: 0px 60px 0px 100px;
    }
  }
  .item-title {
    margin-bottom: 15px;
  }
}

@media (max-width: 1024px) {
  .why-choose__grid {
    gap: 24px;
  }

  .why-choose__item {
    flex-direction: column;
    align-items: flex-start;

    &-img {
      width: 100%;
      max-width: 280px;
      height: 240px;
      margin-right: 0;
      margin-bottom: 32px;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 0px 60px 0px 120px;
      }
    }
  }
}

@media (max-width: 600px) {
  .why-choose__grid {
    grid-template-columns: 1fr;
    gap: 32px;
  }

  .why-choose__item-img {
    max-width: 100%;
    height: 240px;
  }
}

@media (max-width: 480px) {
  .why-choose__container {
    padding: 48px 24px;
  }
}

@media (max-width: 375px) {
  .why-choose__item-img {
    height: 200px;
    margin-bottom: 24px;
  }
}
