.contacts-us {
  width: 100%;
  margin: 0 auto;
  margin-top: -60px;
  padding-bottom: 100px;

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 60px;
    padding: 60px;
    position: relative;
    &::before {
      display: inline-block;
      content: "";
      width: 85%;
      height: 100%;
      border-radius: 24px 24px 0px 0px;
      border-bottom: 1px solid var(--violet-light, #8668e3);
      background: rgba(11, 11, 12, 0.4);
      backdrop-filter: blur(22.5px);
      position: absolute;
      top: 0;
      left: 0;
      z-index: 0;
    }
  }

  //! Contacts Form
  &__form {
    position: relative;
    z-index: 1;
    .form__input {
      height: 50px;
    }
    .form__textarea {
      height: 100px;
    }
  }

  &__title {
    font-size: 24px;
    font-weight: 400;
    text-align: center;
    margin-bottom: 30px;
  }

  //! Contact Collumn
  &__contact {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 30px;
    border-radius: 0px 24px 24px 0px;
    border-left: 1px solid var(--violet-color);
    background: rgba(134, 104, 227, 0.2);
    backdrop-filter: blur(20.5px);
    font-size: 20px;
    font-weight: 400;
    padding: 60px 30px;
  }

  &__contact-item {
    display: flex;
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  &__contact-icon {
    flex-shrink: 0;
    display: inline-block;
    width: 24px;
    height: 24px;
    fill: var(--violet-color);
    margin-right: 15px;
  }

  &__contact-info {
    font-weight: 500;
  }

  &__contac-location {
    margin-bottom: 10px;
  }

  &__contac-address {
    font-weight: 400;
    opacity: 0.8;
  }

  &__contact-footer {
    border-top: 1px solid rgba(#fff, 0.12);
    padding-top: 30px;
  }

  &__contact-label {
    margin-bottom: 10px;
  }

  &__contact-user {
    display: flex;
    flex-direction: column;
  }

  &__contact-name {
    display: inline-block;
    margin-bottom: 10px;
  }

  &__contact-email {
    display: inline-flex;
    align-items: center;
    gap: 12px;
    .ic-email {
      display: inline-block;
      width: 24px;
      height: 24px;
      fill: #fff;
    }
  }
}

@media (max-width: 992px) {
  .contacts-us__wrapper {
    padding: 40px;
    gap: 30px;
  }
}

@media (max-width: 800px) {
  .contacts-us__wrapper {
    grid-template-columns: 1fr;
    gap: 0;
    padding: 0;

    &::before {
      display: none;
    }
  }

  .contacts-us__form {
    padding: 60px 30px;
    border-radius: 24px 24px 0px 0px;
    border-bottom: none;
    background: rgba(11, 11, 12, 0.4);
    backdrop-filter: blur(22.5px);

    .form-contacts,
    .contacts-us__title {
      width: 100%;
      max-width: 480px;
      margin: 0 auto;
    }

    .contacts-us__title {
      margin-bottom: 30px;
    }
  }

  .contacts-us__contact {
    border-left: none;
    border-bottom: 1px solid var(--violet-light, #8668e3);
    border-radius: 24px 24px 0 0;
  }
}

@media (max-width: 480px) {
  .contacts-us__form {
    padding-top: 30px;
  }

  .contacts-us__title {
    font-size: 20px;
    margin-bottom: 20px;
  }
}
