.c-dropdown {
  cursor: pointer;
  position: relative;

  &__label {
    color: rgba(#fff, 0.6);
    font-size: 16px;
    font-weight: 500;
    line-height: 1;
    margin-bottom: 12px;
  }

  &__selected {
    cursor: pointer;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    padding-top: 12px;
  }

  &__inner {
    width: 100%;
    max-height: 220px;
    overflow-y: auto;
    min-width: 160px;
    background: rgba(#220d2a, 0.87);
    backdrop-filter: blur(5px);
    border: 1px solid rgba(#fff, 0.05);
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 999;
    transform: translateY(10px);
    animation: fade 0.2s ease-in-out;
    display: none;

    &::-webkit-scrollbar {
      width: 5px;
    }

    &::-webkit-scrollbar-track {
      background: rgba(#fff, 0.15);
    }

    &::-webkit-scrollbar-thumb {
      background-color: var(--violet-color);
      border-radius: 0;
      border: 0px solid transparent;
    }
  }

  &__search-wrapper {
    padding: 10px 15px;
  }

  &__search {
    outline: none;
    width: 100%;
    height: 32px;
    color: #fff;
    background: transparent;
    border: 1px solid rgba(#fff, 0.4);
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    padding: 0 7px;
    border-radius: 0;
    transition: all 0.15s ease-in-out;
    &:hover {
      border-color: #fff;
    }

    &:focus {
      border-color: var(--violet-color);
    }

    &::placeholder {
      color: rgba(#fff, 0.7);
    }
  }

  &__menu {
    list-style: none;
    width: 100%;
  }

  &__option {
    cursor: pointer;
    width: 100%;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.2;
    padding: 10px 15px;
    opacity: 0.6;
    transition: all 0.3s ease-in-out;
    position: relative;

    &::after {
      display: inline-block;
      content: "";
      width: 14px;
      height: 14px;
      background: url("data:image/svg+xml;base64,")
        no-repeat center;
      background-size: cover;
      position: absolute;
      top: 50%;
      right: 15px;
      transform: translateY(-50%);
      animation: fade 0.2s ease-in-out;
      display: none;
    }

    &:hover {
      opacity: 1;
      background: rgba(#fff, 0.1);
    }

    &.active {
      color: #fff;
      opacity: 1;
      &::after {
        display: block;
      }
    }
  }
}

//  
.c-dropdown.open .c-dropdown__inner {
  display: block;
}
