.btn-arrow {
  cursor: pointer;
  display: inline-block;
  width: 72px;
  height: 72px;
  background-color: none;
  border: 1px solid rgba(#fff, 0.3);
  border-radius: 50%;
  position: relative;
  transition: all 0.3s ease-in-out;

  &__icon {
    position: absolute;
    top: -24px;
    left: 50%;
    transform: translateX(-50%);
  }
}
