.currency-dropdown {
  display: inline-flex;
  align-items: center;
  position: relative;

  .ic-currency {
    flex-shrink: 0;
    width: 28px;
    height: 28px;
  }

  &__select {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    gap: 10px;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    position: relative;

    &::after {
      content: "";
      display: inline-block;
      border: 6px solid transparent;
      border-top: 6px solid #fff;
      position: absolute;
      top: 12px;
      right: -22px;
      transition: transform 0.3s ease-in-out;
    }
  }

  &__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    width: 100%;
    min-width: 170px;
    background: rgba(27, 11, 33, 0.9);
    box-shadow: 0px 10px 24px 8px rgba(32, 20, 46, 0.3);
    backdrop-filter: blur(5px);
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 100;
    display: none;
    animation: fade 0.2s ease-in-out;

    &.show {
      display: flex;
    }
  }

  &__option {
    cursor: pointer;
    @extend .currency-dropdown__select;
    padding: 10px 20px;
    transition: all 0.3s ease-in-out;

    &:hover {
      background: rgba(#fff, 0.1);
    }

    &.active {
      background: var(--purple-color);
    }

    &::after {
      display: none;
    }
  }

  &.open .currency-dropdown__list {
    display: flex;
  }

  &.open .currency-dropdown__select::after {
    transform: rotateX(180deg) translateY(50%);
  }
}
