.contact-form {
  width: 100%;
  border-radius: 24px 24px 0px 0px;
  background: rgba(134, 104, 227, 0.1);
  padding: 40px 20px;

  &__title {
    color: #fff;
    font-family: var(--font-title);
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 30px;
  }

  &__user {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
  }

  &__user-photo {
    flex-shrink: 0;
    width: 72px;
    height: 72px;
    border-radius: 50%;
    margin-right: 20px;
    overflow: hidden;
  }

  &__user-contacts {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__user-contact {
    display: inline-flex;
    align-items: center;
  }

  .contact-icon {
    flex-shrink: 0;
    display: inline-block;
    width: 20px;
    height: 20px;
    fill: #fff;
    margin-right: 12px;
    vertical-align: middle;
  }

  .contact-link {
    color: #fff;
    font-size: 18px;
    font-weight: 400;
    &:hover {
      text-decoration: none;
      color: var(--main-color);
    }
  }

  .form__input {
    background: transparent;
    height: 48px;
  }

  .form__btn {
    width: 100%;
    height: 48px;
    font-size: 20px;
  }

  .check__label {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .contact-form__title {
    text-align: center;
  }
  .contact-form__user {
    flex-direction: column;
  }

  .contact-form__user-photo {
    margin-right: 0;
    margin-bottom: 24px;
  }
}
