.featured-properties {
  padding-top: clamp(3.75rem, 1.813rem + 9.69vw, 8.75rem);
}

// Slider Properties
.properties-inner {
  width: 100%;
  max-width: var(--container-width);
  height: auto;
  position: relative;
}

.properties-slider {
  display: flex;
  width: 100%;
  max-width: var(--container-width);
  height: auto;
  overflow: hidden;
}

.properties-slider .swiper-wrapper {
  box-sizing: content-box;
}

.properties-slider__slide {
  display: flex;
  justify-content: center;
  align-items: center;
  width: auto;
  height: auto;
}

.properties-slider__btns {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 60px;
}

.slider-btn {
  cursor: pointer;
  display: inline-block;
  width: 93px;
  height: 74px;
  font-size: 0;
  transition: all 0.3s ease-in-out;

  &:hover {
    opacity: 0.8;
  }

  &.btn-prev {
    background: url("../img/btn-prev.svg") no-repeat center;
    background-size: cover;
  }

  &.btn-next {
    background: url("../img/btn-next.svg") no-repeat center;
    background-size: cover;
  }
}

@media (max-width: 480px) {
  .properties-slider__btns {
    padding-top: 32px;
  }
}
