.submit-property {
  position: relative;

  &__bg {
    width: 100%;
    min-height: 420px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    opacity: 0.2;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    z-index: -1;
  }

  &__container {
    display: grid;
    grid-template-columns: 7fr 5fr;
    align-items: center;
    gap: 32px;
  }

  &__title {
    margin-bottom: 0;
    position: relative;
    &::after {
      content: "";
      width: 100%;
      max-width: 500px;
      height: 1px;
      background: var(--violet-color);
      position: absolute;
      bottom: -30px;
      left: -128px;
    }
  }

  &__content {
    width: 100%;
    flex: 1 1 626px;
  }

  &__title {
    font-family: var(--font-title);
    font-size: clamp(1.75rem, 1.459rem + 1.45vw, 2.5rem);
    font-weight: 400;
    line-height: 1.4;
    margin-bottom: 60px;
  }

  &__btns {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-top: 70px;
  }

  &__btn {
    display: inline-block;
    height: 56px;
    line-height: 56px;
    color: #fff;
    font-size: 24px;
    font-weight: 500;
    text-transform: uppercase;
    padding: 0 30px;
    transition: all 0.3s ease-in-out;

    &.btn-submit {
      background: #220d2a;
      &:hover {
        background: var(--main-color);
      }
    }

    &.btn-browse {
      background: transparent;
      border: 1px solid #fff;
      &:hover {
        color: #fff;
        border-color: var(--violet-color);
      }
    }
  }

  &__img {
    img {
      border-radius: 300px 300px 0 0;
    }
  }
}

@media (max-width: 1024px) {
  .submit-property__bg {
    min-height: 540px;
  }

  .submit-property__container {
    grid-template-columns: repeat(2, 1fr);
    padding: 0 32px;
    gap: 24px;
  }

  .submit-property__btns {
    display: inline-flex;
    flex-direction: column;
    margin-top: 0;
  }

  .submit-property__btn {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .submit-property {
    overflow: hidden;
    padding-top: 70px;
    padding-bottom: 70px;
  }
  .submit-property__bg {
    min-height: 700px;
    top: 220px;
  }

  .submit-property__title::after {
    width: 30%;
    max-width: inherit;
    left: 50%;
    transform: translateX(-50%);
  }

  .submit-property__container {
    grid-template-columns: 1fr;
    gap: 32px;
  }

  .submit-property__content {
    text-align: center;
  }

  .submit-property__btns {
    display: flex;
    width: 100%;
    max-width: 360px;
    margin: 0 auto;
  }

  .submit-property__img {
    width: 100%;
    max-width: 460px;
    margin: 0 auto;
  }
}

@media (max-width: 480px) {
  .submit-property__container {
    padding: 0 24px;
  }
}

@media (max-width: 375px) {
  .submit-property__btn {
    font-size: 20px;
  }
}
