.floating-currency {
  display: inline-flex;
  flex-direction: column;
  position: fixed;
  right: -42px;
  top: 20%;
  z-index: 150;
  transition: right 0.3s ease-in-out;

  &:hover {
    right: 0;
  }

  @media (max-width: 480px) {
    top: 100px;
  }
}

//! currency-switcher -   
.currency-switcher {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
  position: relative;
  transition: 0.2s all;

  &.open .currency-switcher__options {
    display: flex;
  }

  &__select {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    background: rgba(11, 11, 12, 0.4);
    backdrop-filter: blur(5px);
    border-left: 1px solid #8668e3;
    padding: 10px;
  }

  &__options {
    cursor: pointer;
    // display: flex;
    flex-direction: column;
    background: rgba(11, 11, 12, 0.4);
    backdrop-filter: blur(5px);
    display: none;
    animation: fade 0.3s ease-in-out;
  }

  &__option {
    @extend .currency-switcher__select;
    border-left: 1px solid transparent;
    padding: 10px;
    transition: background 0.3s ease-in-out;

    &.hover {
      background: rgba(#fff, 0.1);
    }

    &.selected {
      background: var(--purple-color);
      border-left: 1px solid #fff;
    }

    .currency-flag {
      width: 24px;
      height: 24px;
    }
  }
}

//! currency-flag
.currency-flag {
  flex-shrink: 0;
  display: inline-block;
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &.currency-flag-usd {
    background-image: url("../img/icons/flag-usd.png");
  }

  &.currency-flag-eur {
    background-image: url("../img/icons/flag-eur.png");
  }

  &.currency-flag-gbp {
    background-image: url("../img/icons/flag-gbp.png");
  }

  &.currency-flag-btc {
    background-image: url("../img/icons/flag-btc.png");
  }

  &.currency-flag-thb {
    background-image: url("../img/icons/flag-thb.png");
  }
}
