html {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  &::before,
  &::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-style: normal;
}

.page {
  height: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  color: #fff;
  background: var(--body-bg-color);
  font-family: var(--font-default, sans-serif);
  font-size: 18px;
  // font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  overflow-x: hidden;

  &.lock-scroll {
    overflow: hidden;
  }
}

img {
  display: inline-block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
  vertical-align: middle;
}

a {
  color: inherit;
  text-decoration: none;
  transition: color 0.2s ease-in-out;
  &:hover {
    color: var(--main-color);
  }
}

.site-container {
  overflow: hidden; //     position: sticky -   
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  width: 100%;
  margin: 0 auto;
  max-width: var(--container-width);
  // padding: 0 var(--container-offset);
  padding: 0 15px;

  @media (min-width: 480px) {
    padding: 0 24px;
  }

  @media (min-width: 768px) {
    padding: 0 32px;
  }

  @media (min-width: 1024px) {
    padding: 0 48px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.hidden {
  display: none;
}

//     
// animation: fade 0.3s ease-in-out; (     ;
@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.text-accent {
  color: var(--violet-color);
}

.text-center {
  text-align: center;
}

.section {
  // padding-top: 140px;
  // padding: 70px 0;
  padding: clamp(2rem, 1.08rem + 4.6vw, 4.375rem) 0;
}

.section-head {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  text-align: center;
  margin-bottom: clamp(2rem, 0.838rem + 5.81vw, 5rem);
  .section-title {
    margin-bottom: 30px;
  }
}

.section-title {
  font-family: var(--font-title);
  // font-size: 60px;
  font-size: clamp(1.5rem, 1.322rem + 3.39vw, 3.75rem);
  font-weight: 400;
  line-height: 1.3;
  text-align: center;
  text-transform: Capitalize;
  position: relative;
  &::after {
    content: "";
    width: 100%;
    max-width: 40%;
    height: 1px;
    background: var(--violet-color);
    position: absolute;
    bottom: -3px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.section-descr {
  font-family: var(--font-default);
  font-size: 20px;
  font-weight: 400;
  line-height: 1.5;
  text-align: center;
  @media (max-width: 480px) {
    font-size: 18px;
  }
}

.item-title {
  font-family: var(--font-title);
  font-size: 24px;
  font-weight: 400;
  line-height: 1.3;
  margin-bottom: 20px;
}

.item-text {
  font-size: 18px;
  line-height: 1.4;
  font-weight: 400;
  opacity: 0.85;
  @media (max-width: 360px) {
    font-size: 16px;
  }
}
