.faq {
  padding-bottom: 140px;

  .page-dropdown {
    margin-bottom: 70px;
  }

  &__heading {
    @extend .news-post__heading;
  }

  &__heading-title {
    @extend .news-post__title;
    width: 100%;
    max-width: 720px;
  }

  font-size: 18px;
}

//! faq-steps
.faq-steps {
  padding-bottom: clamp(3.75rem, 2.338rem + 7.06vw, 7.5rem);

  &__top,
  &__bottom {
    // display: flex;
    // justify-content: space-between;
    display: grid;
    gap: 30px;
  }

  &__top {
    grid-template-columns: repeat(4, 1fr);
    margin-bottom: 50px;
  }

  &__bottom {
    grid-template-columns: repeat(3, 1fr);
  }

  &__item {
    // padding: 0 15px;
    text-align: center;
    font-size: 20px;
    font-weight: 400;
    line-height: 1.3;
  }

  &__item-number {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 140px;
    height: 140px;
    background: url("../img/faq-circle-bg.svg") no-repeat center;
    background-size: cover;
    font-size: 60px;
    font-weight: 300;
    margin-bottom: 24px;
    position: relative;
    z-index: 0;
    span {
      background: var(--accent-gradient);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    //! Lines
    &::before {
      display: inline-block;
      content: "";
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: cover;
      z-index: -1;
      position: absolute;
    }
    &::after {
      display: inline-block;
      content: "";
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: cover;
      z-index: -1;
      position: absolute;
    }

    &.faq-number_1::before,
    &.faq-number_5::before {
      width: 221px;
      height: 59px;
      background-image: url("../img/faq-lines/faq-line_5.svg");
      top: 87px;
      left: -100%;
      transform: rotate(0deg);
      transform: translateX(-100px);
    }

    &.faq-number_5::before {
      width: 226px;
      height: 40px;
      background-image: url("../img/faq-lines/faq-line_9.svg");
      top: 41px;
      left: -100%;
      transform: rotate(0deg);
      transform: translateX(-100px);
    }

    &.faq-number_1::after {
      width: 152px;
      height: 40px;
      background-image: url("../img/faq-lines/faq-line_2.svg");
      top: -20px;
      right: -100%;
      transform: rotate(10deg);
    }

    &.faq-number_2::after {
      width: 158px;
      height: 40px;
      background-image: url("../img/faq-lines/faq-line_3.svg");
      bottom: -10px;
      right: -100%;
      transform: rotate(0deg);
      transform: translateX(10px);
    }

    &.faq-number_3::after {
      width: 152px;
      height: 40px;
      background-image: url("../img/faq-lines/faq-line_4.svg");
      top: -10px;
      right: -100%;
      transform: rotate(0deg);
      transform: translateX(10px);
    }

    &.faq-number_4::after,
    &.faq-number_7::after {
      width: 221px;
      height: 59px;
      background-image: url("../img/faq-lines/faq-line_5.svg");
      bottom: -10px;
      right: -100%;
      transform: rotate(0deg);
      transform: translateX(83px);
    }

    &.faq-number_5::after {
      width: 210px;
      height: 34px;
      background-image: url("../img/faq-lines/faq-line_6.svg");
      top: -10px;
      right: -100%;
      transform: rotate(0deg);
      transform: translateX(83px);
    }

    &.faq-number_6::after {
      width: 221px;
      height: 59px;
      background-image: url("../img/faq-lines/faq-line_5.svg");
      bottom: -10px;
      right: -100%;
      transform: rotate(0deg);
      transform: translateX(83px);
    }
  }
}

//! Accordion
.accordion {
  // item 
  &__item {
    transition: all 0.3s ease-in-out;
    &:not(:last-child) {
      // margin-bottom: 50px;
      margin-bottom: 90px;
    }
  }

  //        
  &__head {
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    transition: all 0.3s ease-in-out;
  }

  // 
  &__title {
    @extend .news-post__title;
    width: 100%;
    max-width: 1000px;
    transition: color 0.3s ease-in-out;
    margin-bottom: 0;

    &:hover {
      color: var(--main-color);
    }
  }

  // 
  &__icon {
    flex-shrink: 0;
    display: inline-block;
    width: 64px;
    height: 64px;
    background: url("../img/icons/ic-faq_plus.svg") no-repeat center;
    background-size: cover;
  }

  // 
  &__body {
    box-sizing: content-box;
    padding: 0;
    font-size: 22px;
    line-height: 1.3;
    max-height: 0;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
  }
}

//    
.accordion__head.active .accordion__title {
  color: var(--main-color);
}

.accordion__head.active .accordion__icon {
  background: url("../img/icons/ic-faq_minus.svg") no-repeat center;
  background-size: cover;
}

.accordion__body.active {
  visibility: visible;
  opacity: 1;
  padding-top: 60px;
}

/* ------------------- Adaptive -------------------- */
@media (max-width: 1024px) {
  .faq-steps__top {
    grid-template-columns: repeat(2, 1fr);
    gap: 24px;
    row-gap: 40px;
  }

  .faq-steps__bottom {
    grid-template-columns: repeat(2, 1fr);
    gap: 24px;
    row-gap: 40px;
  }

  .faq-steps__item-number {
    &::before,
    &::after {
      display: none !important;
    }
  }

  .faq-steps__item-number {
    width: clamp(6rem, 5.318rem + 3.41vw, 7.5rem);
    height: clamp(6rem, 5.318rem + 3.41vw, 7.5rem);
    font-size: clamp(2.5rem, 1.932rem + 2.84vw, 3.75rem);
  }
}

@media (max-width: 768px) {
  .accordion__title {
    font-size: 32px;
  }

  .accordion__body {
    font-size: 18px;
  }

  .accordion__item:not(:last-child) {
    margin-bottom: 70px;
  }
}

@media (max-width: 600px) {
  .faq-steps__item {
    font-size: 18px;
  }

  .accordion__icon {
    width: 48px;
    height: 48px;
  }
}

@media (max-width: 480px) {
  .faq {
    padding-bottom: 80px;
  }

  .faq__heading {
    padding-bottom: 20px;
  }

  .faq-steps__top,
  .faq-steps__bottom {
    grid-template-columns: 1fr;
    gap: 30px !important;
  }

  .faq-steps__top {
    margin-bottom: 25px;
  }

  .faq-steps__item {
    display: flex;
    align-items: center;
    text-align: left;
  }

  .faq-steps__item-number {
    width: 60px;
    height: 60px;
    flex-shrink: 0;
    font-size: 30px;
    margin-bottom: 0;
    margin-right: 20px;
  }

  .accordion__item:not(:last-child) {
    margin-bottom: 50px;
  }
  .accordion__title {
    font-size: 24px;
  }

  .accordion__title::after {
    width: 80px;
    bottom: -14px;
  }

  .accordion__body.active {
    padding-top: 40px;
  }
}

@media (max-width: 375px) {
  .accordion__title {
    font-family: var(--font-default);
    font-size: 20px;
  }

  .accordion__item:not(:last-child) {
    margin-bottom: 40px;
  }

  .accordion__title::after {
    bottom: -17px;
  }
}
