.page-dropdown {
  width: 100%;
  max-width: 208px;
  margin: 0 auto;
  height: 32px;
  border-bottom: 1px solid var(--violet-color);
  position: relative;
  &::after {
    content: "";
    display: inline-block;
    border: 5px solid transparent;
    border-top: 5px solid var(--violet-color);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  }

  .c-dropdown__inner {
    transform: translateY(1px);
  }

  .c-dropdown__select {
    display: flex;
    align-items: center;
    height: 100%;
  }

  .c-dropdown__selected {
    padding-top: 0;
    color: rgba(#fff, 0.65);
  }

  &.open::after {
    transform: rotateX(180deg) translateY(50%);
    border-top: 5px solid #fff;
  }
}
