.properties {
  padding-top: 40px;
  padding-bottom: 70px;

  .page-dropdown {
    margin-bottom: 60px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;

    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  &__hide {
    display: none;
    animation: fade 0.3s ease-in-out;

    &.show {
      display: grid;
    }
  }
}

/* ------------------- Adaptive -------------------- */

@media (max-width: 1024px) {
  .properties__list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 640px) {
  .properties__list {
    grid-template-columns: 1fr;
  }
}
