.breadcrumb {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  padding: 30px 0;

  &__menu {
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
  }

  &__item {
    color: rgba(#fff, 0.65);
    &:not(:last-child) {
      margin-right: 10px;
    }

    &::after {
      content: "";
      display: inline-block;
      width: 6px;
      height: 11px;
      background: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iOCIgaGVpZ2h0PSIxMyIgdmlld0JveD0iMCAwIDggMTMiIGZpbGw9Im5vbmUiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+CiAgPHBhdGggaWQ9IjRhZTEwYjM3IiBkPSJNMSAxTDcgNi41TDEgMTIiIHN0cm9rZT0id2hpdGUiPjwvcGF0aD4KPC9zdmc+")
        no-repeat center;
      background-size: cover;
      margin-left: 10px;
      color: #fff;
      position: relative;
      top: 1px;
    }

    &.active::after {
      display: none;
    }
  }

  &__link {
    color: inherit;
    text-decoration: none;

    &:hover {
      color: var(--main-color);
      text-decoration: underline;
    }
  }
}
