.our-service {
  padding-bottom: clamp(3.75rem, 1.813rem + 9.69vw, 8.75rem);

  &__title {
    margin-bottom: 30px;
  }

  &__subtitle {
    color: var(--violet-color);
    text-align: center;
    font-family: var(--font-title);
    // font-size: 40px;
    font-size: clamp(1.75rem, 1.459rem + 1.45vw, 2.5rem);
    font-weight: 400;
    line-height: 1.2;
    text-transform: Capitalize;
    margin-bottom: 30px;
  }

  &__descr {
    font-size: 20px;
    font-weight: 400;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }

  &__item {
    background: rgba(#8668e3, 0.1);
    border-radius: 20px 20px 0 0;
    border-bottom: 1px solid var(--light-blue_3);
    text-align: center;
    padding: 30px;
  }

  &__item-icon {
    display: inline-block;
    margin-bottom: 20px;
  }

  .item-title {
    position: relative;

    &::after {
      content: "";
      width: 100%;
      max-width: 64px;
      height: 1px;
      background: var(--violet-color);
      position: absolute;
      bottom: -8px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}

@media (max-width: 1024px) {
  .our-service__grid {
    gap: 24px;
  }
}

@media (max-width: 767px) {
  .our-service__grid {
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    grid-template-columns: 1fr;
  }

  .our-service__item {
    padding: 24px;
  }
}
