.news-page {
  @media (max-width: 480px) {
    .page-header {
      min-height: 320px;
    }
  }
}

.news-page-article {
  .page-header__title {
    font-size: clamp(2rem, 0.596rem + 7.02vw, 5rem);
  }
}

.news {
  padding-bottom: 100px;
  .page-dropdown {
    margin-bottom: 60px;
  }

  &__list {
    @extend .properties__list;
  }

  &__hide {
    @extend .properties__hide;
  }
}

@media (max-width: 599px) {
  .news {
    padding-bottom: 70px;
  }
}
